.advantages {
  padding: 108px calc(50% - 501px) 121px;
  background-color: $color-ghost-white;

  @include vp-1199 {
    padding: 91px 45px;
  }

  @include vp-767 {
    padding: 70px 0 65px;
  }
}

.advantages__title {
  margin: 0 0 49px;
  text-align: center;
}

.advantages__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 321px 240px 335px;
  gap: 150px 60px;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    gap: 41px 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 37px;
  }

